import { FC, useState } from "react";
import {
  Button,
  WriteComment,
  LightButton,
  UniversalModal,
} from "@components/common";
import { ViewTaskModalProps } from "./types-view-task-modal";
import {
  AddToCalendar,
  BottomAboutProjectCard,
  BottomButtonCard,
  ButtonCard,
  Card,
  CommentAndStoryAboutTaskBox,
  Day,
  FinishTime,
  FinishTimeBox,
  Icon,
  RepetitionBox,
  StartTime,
  StartTimeBox,
  StartToFinishBox,
  SubmitButtonBox,
  TaskName,
  TaskNameBox,
  Time,
  Title,
  Wrapper,
} from "./styles-view-task-modal";
import closeIcon from "@assets/images/dashboard/chat/close.svg";
import sawIcon from "@assets/images/dashboard/chat/eyeBlue.svg";
import upload from "@assets/images/dashboard/upload.svg";
import flagRed from "@assets/images/dashboard/tasks/flag-2-fill 1.svg";
import flagYellow from "@assets/images/dashboard/tasks/flag-2-fill 2.svg";
import flagGreen from "@assets/images/dashboard/tasks/flag-2-fill 3.svg";
import { styled, Switch, SwitchProps } from "@mui/material";
const ViewTaskModal: FC<ViewTaskModalProps> = ({ open, handleClose }) => {
  const [repeatValue, setRepeatValue] = useState<string>("");
  const btnDatas = [
    { value: "Ibrohim Yuldashev", title: "Ijrochi" },
    { value: "Ibrohim ", title: "Rahbar" },
    { value: "New", title: "Status" },
    { value: "Yuldashev", title: "Muallif" },
  ];
  const IOSSwitch = styled((props: SwitchProps) => (
    <Switch
      focusVisibleClassName=".Mui-focusVisible"
      disableRipple
      {...props}
    />
  ))(({ theme }) => ({
    width: 42,
    height: 26,
    padding: 0,
    "& .MuiSwitch-switchBase": {
      padding: 0,
      margin: 2,
      transitionDuration: "300ms",
      "&.Mui-checked": {
        transform: "translateX(16px)",
        color: "#fff",
        "& + .MuiSwitch-track": {
          backgroundColor:
            theme.palette.mode === "dark" ? "#2ECA45" : "#65C466",
          opacity: 1,
          border: 0,
        },
        "&.Mui-disabled + .MuiSwitch-track": {
          opacity: 0.5,
        },
      },
      "&.Mui-focusVisible .MuiSwitch-thumb": {
        color: "#33cf4d",
        border: "6px solid #fff",
      },
      "&.Mui-disabled .MuiSwitch-thumb": {
        color:
          theme.palette.mode === "light"
            ? theme.palette.grey[100]
            : theme.palette.grey[600],
      },
      "&.Mui-disabled + .MuiSwitch-track": {
        opacity: theme.palette.mode === "light" ? 0.7 : 0.3,
      },
    },
    "& .MuiSwitch-thumb": {
      boxSizing: "border-box",
      width: 22,
      height: 22,
    },
    "& .MuiSwitch-track": {
      borderRadius: 26 / 2,
      backgroundColor: theme.palette.mode === "light" ? "#E9E9EA" : "#39393D",
      opacity: 1,
      transition: theme.transitions.create(["background-color"], {
        duration: 500,
      }),
    },
  }));

  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
        <img src={closeIcon} onClick={handleClose} alt="close icon" />
        <h1>Vazifa</h1>
        <ButtonCard>
          {btnDatas.map((item, idx) => (
            <div key={idx}>
              <h1>{item.title}</h1>
              <LightButton
                style={{ background: "#0496FF", color: "#fff" }}
                text={item.value}
              />
            </div>
          ))}
        </ButtonCard>
        <TaskNameBox>
          <h4>Vazifa nomi</h4>
          <TaskName>
            <h1>Bu yilgi vazifa</h1>
          </TaskName>
        </TaskNameBox>
        <StartToFinishBox>
          <StartTimeBox>
            <h3>Boshlanish vaqti</h3>
            <StartTime>
              <Time>
                <h1>14:00</h1>
              </Time>
              <Day>
                <h1>24.04.2022</h1>
              </Day>
            </StartTime>
          </StartTimeBox>
          <FinishTimeBox>
            <h3>Tugatish vaqti</h3>
            <FinishTime>
              <Time>
                <h1>14:00</h1>
              </Time>
              <Day>
                <h1>24.04.2022</h1>
              </Day>
            </FinishTime>
          </FinishTimeBox>
        </StartToFinishBox>
        <BottomButtonCard>
          <LightButton
            icon={<Icon src={upload} border_radius />}
            text={"Fayllar"}
            score={4}
          />
          <LightButton
            icon={<Icon src={sawIcon} border_radius />}
            text={"Kuzatuvchilar"}
            score={4}
          />
          <LightButton
            icon={<Icon src={flagYellow} border_radius />}
            text={"Muhimlik"}
          />
        </BottomButtonCard>
        <BottomAboutProjectCard>
          <Card>
            <h2>New proyekt</h2>
          </Card>
          <Card>
            <h2>Kiruvchi vazifalar</h2>
          </Card>
          <Card>
            <h2>Bir martalik</h2>
          </Card>
        </BottomAboutProjectCard>
        <CommentAndStoryAboutTaskBox>
          <WriteComment />
        </CommentAndStoryAboutTaskBox>
        <SubmitButtonBox>
          <Button value="O'chirish" type="submit" backColor="#D92A2A" />
          <AddToCalendar>
            <Title>Kalendarga qo’shish</Title>
            <IOSSwitch sx={{ m: 1 }} defaultChecked />
          </AddToCalendar>
        </SubmitButtonBox>
      </Wrapper>
    </UniversalModal>
  );
};

export default ViewTaskModal;
