import { FC, useState } from "react";
import { TasksProps } from "./types-tasks";
import {
  LeftLightButtons,
  LightButtonsBox,
  RightLightButtons,
  Wrapper,
} from "./styles-tasks";
import { GeneralContainer, LightButton } from "@components/common";
import plusIcon from "@assets/images/dashboard/tasks/plus.svg";
import FunctionalTasksList from "./tasks-list/functional-tasks-list";
import AddDragDropModal from "./add-column-modal/add-drag-drop-modal";
const Tasks: FC<TasksProps> = () => {
  const [openAdd, setOpenAdd] = useState(false);
  const [openAddIsColumn, setOpenAddIsColumn] = useState(false);
  const [activeId, setActiveId] = useState(0);

  const taskTypes = [
    { title: "Funksional" },
    { title: "Proyektlar" },
    { title: "Taktik reja" },
  ];
  const handleOpenAdd = (isColumn: boolean) => {
    setOpenAddIsColumn(isColumn);
    setOpenAdd(true);
  };
  const handleCloseAdd = () => {
    setOpenAdd(false);
  };
  const itemsFromBackend = [
    { id: "card-1", content: "First task" },
    { id: "card-2", content: "Second task" },
    { id: "card-3", content: "Third task" },
    { id: "card-4", content: "Fourth task" },
    { id: "card-5", content: "Fifth task" },
    { id: "card-6", content: "Fifth task" },
    { id: "card-7", content: "Fifth task" },
    { id: "card-8", content: "Fifth task" },
  ];

  const columnsFromBackend = {
    lists: {
      "column-1": {
        name: "Requested",
        id: "column-1",
        items: itemsFromBackend,
      },
      "column-2": {
        name: "To do",
        id: "column-2",
        items: [],
      },
      "column-3": {
        name: "In Progress",
        id: "column-3",
        items: [],
      },
      "column-4": {
        name: "Done",
        id: "column-4",
        items: [],
      },
    },
    listIds: ["column-1", "column-2", "column-3", "column-4"],
  };

  const itemsFromBackend2 = [
    { id: "card1-1", content: "First task" },
    { id: "card1-2", content: "Second task" },
    { id: "card1-3", content: "Third task" },
    { id: "card1-4", content: "Fourth task" },
    { id: "card1-5", content: "Fifth task" },
    { id: "card1-6", content: "Fifth task" },
    { id: "card1-7", content: "Fifth task" },
    { id: "card1-8", content: "Fifth task" },
  ];

  const columnsFromBackend2 = {
    lists: {
      "column1-1": {
        name: "Requested",
        id: "column1-1",
        items: itemsFromBackend2,
      },
      "column1-2": {
        name: "To do",
        id: "column1-2",
        items: [],
      },
      "column1-3": {
        name: "In Progress",
        id: "column1-3",
        items: [],
      },
      "column1-4": {
        name: "Done",
        id: "column1-4",
        items: [],
      },
    },
    listIds: ["column1-1", "column1-2", "column1-3", "column1-4"],
  };
  return (
    <GeneralContainer>
      <AddDragDropModal
        isColumn={openAddIsColumn}
        open={openAdd}
        handleClose={handleCloseAdd}
      />
      <Wrapper>
        <h1>Vazifalar</h1>
        <LightButtonsBox>
          <LeftLightButtons>
            {taskTypes.map((taskType, idx) => (
              <LightButton
                onCLickButton={() => setActiveId(idx)}
                style={
                  activeId === idx
                    ? { background: "#0496ff", color: "#FFFFFF" }
                    : {}
                }
                text={taskType.title}
              />
            ))}
          </LeftLightButtons>
          <RightLightButtons>
            <LightButton
              onCLickButton={() => handleOpenAdd(false)}
              icon={<img src={plusIcon} alt="icon" />}
              text="Vazifalar guruhi qo’shish"
            />
            <LightButton
              onCLickButton={() => handleOpenAdd(true)}
              icon={<img src={plusIcon} alt="icon" />}
              text="Ustun qo’shish"
            />
          </RightLightButtons>
        </LightButtonsBox>
        <FunctionalTasksList columnsFromBackend={columnsFromBackend} />
        <FunctionalTasksList columnsFromBackend={columnsFromBackend2} />
      </Wrapper>
    </GeneralContainer>
  );
};

export default Tasks;
