import { Paper } from "@mui/material";
import { FC, useState } from "react";
import { Draggable } from "react-beautiful-dnd";
import {
  CardContent,
  LeftContent,
  MainWrapper,
  RightContent,
  Wrapper,
} from "./styles-list-item";
import { ListItemProps } from "./types-list-item";
import newIcon from "@assets/images/dashboard/tasks/new.png";
import workIcon from "@assets/images/dashboard/tasks/work.svg";
import clipboardList from "@assets/images/dashboard/tasks/clipboard-board.svg";
import menuIcon from "@assets/images/dashboard/tasks/menu.svg";
import editIcon from "@assets/images/dashboard/tasks/editBlue.svg";
import EditDragDropModal from "../../../../edit-column-modal/edit-drag-drop-modal";
import ViewTaskModal from "@components/common/view-task-modal/view-task-modal";
const ListItem: FC<ListItemProps> = ({ card, index }) => {
  const [open, setOpen] = useState(false);
  const [openViewModal, setOpenViewModal] = useState(false);
  const handleOpenViewModal = () => {
    setOpenViewModal(true);
  };
  const handleCloseViewModal = () => {
    setOpenViewModal(false);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <Wrapper>
      <ViewTaskModal open={openViewModal} handleClose={handleCloseViewModal} />
      <EditDragDropModal open={open} handleClose={handleClose} />
      <Draggable draggableId={card.id} index={index}>
        {(provided) => (
          <div
          ref={provided.innerRef}
          {...provided.dragHandleProps}
          {...provided.draggableProps}
          >
          <MainWrapper onClick={handleOpenViewModal}></MainWrapper>
            <CardContent>
              <LeftContent>
                <h1>
                  <img src={newIcon} alt="icon" />
                  Yangi
                </h1>
                <p> {card.content}</p>
              </LeftContent>
      <RightContent>
        <img src={menuIcon} alt="icon" />
        <img onClick={handleOpen} src={editIcon} alt="icon" />
      </RightContent>
            </CardContent>
          </div>
        )}
      </Draggable>
    </Wrapper>
  );
};

export default ListItem;
