import styled from "styled-components";
export const MainWrapper = styled.div`
  position: absolute;
  max-width: 220px;
  width: 100%;
  height: 50px;
`;
export const Wrapper = styled.div`
  > div[data-rbd-drag-handle-context-id] {
    background: #fff;
    position: relative;
    padding: 10px;
    margin: 15px;
    max-width: 262px;
    width: 100%;    
    border-radius: 7px;
    height: 100%;
  }
`;
export const CardContent = styled.div`
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: space-between;
`;
export const LeftContent = styled.div`
  h1 {
    display: flex;
    align-items: center;
    margin: 0;
    font-size: 16px;
    font-weight: 600;
    img {
      width: 24px;
      height: 24px;
      margin-right: 7px;
    }
  }
  p {
    font-size: 12px;
    font-weight: 400;
    margin-top: 4px;
    margin-left: 30px;
    color: #a5a9b8;
  }
`;
export const RightContent = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    cursor: pointer;
    width: 20px;
    margin-bottom: 5px;
  }
`;
