import { Accordion, AccordionDetails, AccordionSummary } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { FC, useState } from "react";
import {
  ButtonDelete,
  ButtonEdit,
  Buttons,
  TaskAccordionBox,
  Wrapper,
} from "./styles-functional-tasks-list";
import { FunctionalTasksListProps } from "./types-functional-tasks-list";
import editIcon from "@assets/images/dashboard/tasks/editBlue.svg";
import trashIcon from "@assets/images/dashboard/tasks/trash.svg";
import DragDropTasksList from "./drag-drop-tasks-list/drag-drop-tasks-list";
import RemoveModal from "@components/common/remove-modal/remove-modal";
import ViewTaskModal from "@components/common/view-task-modal/view-task-modal";
const FunctionalTasksList: FC<FunctionalTasksListProps> = ({columnsFromBackend}) => {
  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  
  return (
    <Wrapper>
      <RemoveModal
        title="Vazifalar ustunini o'chirishni istaysizmi"
        open={open}
        handleClose={handleClose}
      />
      <h3>Vazifa nomi</h3>
      <TaskAccordionBox>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <h1>Kiruvchi vazifalar nomi</h1>
          </AccordionSummary>
          <AccordionDetails>
            <DragDropTasksList columnsFromBackend={columnsFromBackend}/>
          </AccordionDetails>
        </Accordion>
        <Buttons>
          <ButtonEdit onClick={handleOpen}>
            <img src={editIcon} alt="trash icon" />
          </ButtonEdit>
          <ButtonDelete onClick={handleOpen}>
            <img src={trashIcon} alt="trash icon" />
          </ButtonDelete>
        </Buttons>
      </TaskAccordionBox>
    </Wrapper>
  );
};

export default FunctionalTasksList;
