import { FC, useEffect } from 'react';
import Tasks from '@components/Layouts/tasks/tasks';
import { api } from '@src/services/apis/client';
import { useQuery } from 'react-query';

const TasksPage: FC<{}> = () => {
  // console.log('data tasks page: ', data);
  const fetchCompanies = async () => {
    try {
      let res = await api.get('/tactic-plan/tactic-plan-list/');
      console.log('tactic-plan-list: ', res.data);
      return res.data.data;
    } catch (err: any) {
      throw new Error(err);
    }
  };
  
  useEffect(() => {
    fetchCompanies();
  }, []);
  return <Tasks />;
};

export default TasksPage;
