import { FC, useState } from "react";
import { DragDropContext, Droppable } from "react-beautiful-dnd";
import DraggableElement from "./draggable-element/draggable-element";
import { DroppableWrapper, Wrapper } from "./styles-drag-drop-tasks-list";
import { DragDropTasksListProps } from "./types-drag-drop-tasks-list";

const DragDropTasksList: FC<DragDropTasksListProps> = ({columnsFromBackend}) => {
  const [data, setData]: any = useState(columnsFromBackend);

  const onDragEnd = (result: any) => {
    const { destination, source, draggableId, type } = result;
    if (!destination) {
      return;
    }
    if (type === "list") {
      const newListIds = data.listIds;
      newListIds.splice(source.index, 1);
      newListIds.splice(destination.index, 0, draggableId);
      return;
    }

    const sourceList = data.lists[source.droppableId];
    const destinationList = data.lists[destination.droppableId];
    const draggingCard = sourceList.items.filter(
      (card: any) => card.id === draggableId
    )[0];

    if (
      source.droppableId === destination.droppableId &&
      destination.index === source.index
    ) {
      sourceList.items.splice(source.index, 1);
      destinationList.items.splice(destination.index, 0, draggingCard);
      const newSate = {
        ...data,
        lists: {
          ...data.lists,
          [sourceList.id]: destinationList,
        },
      };

      setData(newSate);
    } else {
      sourceList.items.splice(source.index, 1);
      destinationList.items.splice(destination.index, 0, draggingCard);

      const newState = {
        ...data,
        lists: {
          ...data.lists,
          [sourceList.id]: sourceList,
          [destinationList.id]: destinationList,
        },
      };
      setData(newState);
    }
  };
  return (
    <Wrapper>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="app" type="list" direction="horizontal">
          {(provided) => (
            <div ref={provided.innerRef} {...provided.droppableProps}>
              <DroppableWrapper>
                {data.listIds.map((listId: string, index: number) => {
                  const list = data.lists[listId];
                  return (
                    <DraggableElement list={list} key={listId} index={index} />
                  );
                })}
              </DroppableWrapper>
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </Wrapper>
  );
};

export default DragDropTasksList;
                      