import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  height: max-content;
  background: #fff;
  padding: 30px;
  border-radius: 7px;
  h1 {
    margin-bottom: 30px;
  }
`;
export const LightButtonsBox = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`;
export const LeftLightButtons = styled.div`
  display: flex;
  margin-bottom: 30px;
  button {
    margin-right: 22px;
  }
`;
export const RightLightButtons = styled.div`
  display: flex;
  button {
    margin-right: 22px;
    img {
      margin-right: 15px;
    }
  }
`;
