import { Draggable, Droppable } from "react-beautiful-dnd";
import ListItem from "./list-item/list-item";
import { FC, useState } from "react";
import { DraggableElementProps } from "./types-draggable-element";
import {
  ColumnBox,
  ColumnTopBox,
  ColumnTopButtons,
  Wrapper,
} from "./styles-draggable-element";
import plusIcon from "@assets/images/dashboard/tasks/plus-white.svg";
import editIcon from "@assets/images/dashboard/tasks/edit.svg";
import trashIcon from "@assets/images/dashboard/tasks/trash-white.svg";
import EditDragDropModal from "../../../edit-column-modal/edit-drag-drop-modal";
import AddDragDropModal from "../../../add-column-modal/add-drag-drop-modal";
import RemoveModal from "@components/common/remove-modal/remove-modal";
const DraggableElement: FC<DraggableElementProps> = ({ list, index }) => {
  const [openEdit, setOpenEdit] = useState(false);
  const handleOpenEdit = () => {
    setOpenEdit(true);
  };
  const handleCloseEdit = () => {
    setOpenEdit(false);
  };
  const [openAdd, setOpenAdd] = useState(false);
  const handleOpenAdd = () => {
    setOpenAdd(true);
  };
  const handleCloseAdd = () => {
    setOpenAdd(false);
  };
  const [openRemove, setOpenRemove] = useState(false);
  const handleOpenRemove = () => {
    setOpenRemove(true);
  };
  const handleCloseRemove = () => {
    setOpenRemove(false);
  };
  return (
    <Wrapper>
      <AddDragDropModal open={openAdd} handleClose={handleCloseAdd} />
      <EditDragDropModal
        isColumn={true}
        open={openEdit}
        handleClose={handleCloseEdit}
      />
      <RemoveModal
        title="Vazifalar guruhini o'chirishni istaysizmi"
        open={openRemove}
        handleClose={handleCloseRemove}
      />

      <Draggable draggableId={list.id} index={index}>
        {(provided) => (
          <div
            className="mainColumnWrap"
            ref={provided.innerRef}
            {...provided.dragHandleProps}
            {...provided.draggableProps}
          >
            <ColumnBox>
              <ColumnTopBox>
                <h1>
                  {list.name} <span>5</span>
                </h1>
                <ColumnTopButtons>
                  <img onClick={handleOpenAdd} src={plusIcon} alt="icon" />
                  <img onClick={handleOpenEdit} src={editIcon} alt="icon" />
                  <img onClick={handleOpenRemove  } src={trashIcon} alt="icon" />
                </ColumnTopButtons>
              </ColumnTopBox>
              <Droppable droppableId={list.id}>
                {(provided) => (
                  <div
                    className="columnDiv"
                    ref={provided.innerRef}
                    {...provided.droppableProps}
                  >
                    <div>
                      {list.items.map((card: any, index: number) => (
                        <ListItem  key={card.id} card={card} index={index} />
                      ))}
                    </div>
                    {provided.placeholder}
                  </div>
                )}
              </Droppable>
            </ColumnBox>
          </div>
        )}
      </Draggable>
    </Wrapper>
  );
};

export default DraggableElement;
