import { Button, Input, UniversalModal } from "@components/common";
import { Checkbox, FormControlLabel } from "@mui/material";
import { FC } from "react";
import { useForm } from "react-hook-form";
import { Wrapper } from "./styles-edit-drag-drop-column-modal";
import {
  EditDragDropModalForm,
  EditDragDropModalProps,
} from "./types-edit-drag-drop-column-modal";
const EditDragDropModal: FC<EditDragDropModalProps> = ({
  isColumn,
  open,
  handleClose,
}) => {
  const { handleSubmit, control } = useForm<EditDragDropModalForm>({
    defaultValues: {
      meetingName: "",
      comment: "",
      meetingPlace: "",
    },
  });
  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
        {isColumn ? (
          <h1>Vazifalar ustunini tahrirlash</h1>
        ) : (
          <h1>Vazifalar guruhini tahrirlash</h1>
        )}
        <Input
          title="Nomini kiriting!"
          placeholder={isColumn ? "Ustun nomi" : "Guruh nomi"}
          name="edit"
          control={control}
          onChange={() => console.log("edit input")}
        />
        {isColumn && (
          <FormControlLabel
            control={<Checkbox defaultChecked />}
            label="Hammaga ko’rinsin"
          />
        )}
        <Button value="Saqlash" type="submit" />
      </Wrapper>
    </UniversalModal>
  );
};

export default EditDragDropModal;
