import styled from "styled-components";

export const Wrapper = styled.div`
  margin: 10px 0;
  .columnDiv {
    > div {
      width: 100%;
      min-height: 440px;
      max-height: 440px;
      overflow-y: auto;
      ::-webkit-scrollbar {
        width: 0;
      }
    }
    display: flex;
    height: 100%;
    max-height: 440px;
    min-height: 440px;
    background: #f6f6f6;
    width: 292px;
    border-radius: 7px;
  }
  .mainColumnWrap {
    > div {
      width: 100%;
    }
    display: flex;
    height: 100%;
    background: #f6f6f6;
    border-radius: 7px;
    width: 292px;
    margin: 0 10px;
  }
`;

export const ColumnBox = styled.div``;
export const ColumnTopBox = styled.div`
  display: flex;
  background: #0496ff;
  width: 100%;
  align-items: center;
  padding: 20px 15px;
  justify-content: space-between;
  border-radius: 7px 7px 0 0;
  h1 {
    font-size: 18px;
    font-weight: 600;
    margin: 0;
    color: #fff;
    span {
      color: #ffe515;
    }
  }
`;
export const ColumnTopButtons = styled.div`
  display: flex;
  img {
    margin-left: 15px;
    cursor: pointer;
  }
`;
