import { Button, Input, UniversalModal } from "@components/common";
import { Checkbox, FormControlLabel } from "@mui/material";
import { FC } from "react";
import { useForm } from "react-hook-form";
import { Wrapper } from "./styles-add-drag-drop-column-modal";
import {
  AddDragDropModalForm,
  AddDragDropModalProps,
} from "./types-add-drag-drop-column-modal";
const AddDragDropModal: FC<AddDragDropModalProps> = ({
  isColumn,
  open,
  handleClose,
}) => {
  const { handleSubmit, control } = useForm<AddDragDropModalForm>({
    defaultValues: {
      meetingName: "",
      comment: "",
      meetingPlace: "",
    },
  });
  return (
    <UniversalModal open={open} handleClose={handleClose}>
      <Wrapper>
        {isColumn ? (
          <h1>Vazifalar ustunini qo’shish</h1>
        ) : (
          <h1>Vazifalar guruhi qo’shish</h1>
        )}
        <Input
          title="Nomini kiriting!"
          placeholder={isColumn?"Ustun nomi":"Guruh nomi"}
          name="edit"
          control={control}
          onChange={() => console.log("edit input")}
        />
        {isColumn && (
          <FormControlLabel
            control={<Checkbox defaultChecked />}
            label="Hammaga ko’rinsin"
          />
        )}
        <Button value="Qo’shish" type="submit" />
      </Wrapper>
    </UniversalModal>
  );
};

export default AddDragDropModal;
